import React from 'react';
import './App.css';

import ContentCard from './ContentCard';
import { Navbar } from 'react-bootstrap';
import * as yfinance from 'yahoo-finance';
import { render } from '@testing-library/react';

const symbol = require('yahoo-finance-scraper')

class LandPriceCard extends ContentCard {
  constructor() {
    super();
    this.state = { data: [] };
  }

  async componentDidMount() {
    try {
      //this.state = { data: [] };
      const response = await fetch(`https://api.jonghyunho.com/v1/chartGetHousePrice`);
      const json = await response.json();
      this.setState({ data: json });
    } catch (error) {
      console.log(error);
    }
  }

  getValues() {
    var indices = [];
    var values = [];

    var result = this.state.data;

    for (var index in result.prices) {
      if (index % 4 == 0) {
        indices.push(result.prices[index][0]);
      } else {
        indices.push('');
      }      
      values.push(result.prices[index][1].toFixed(2));
    }

    return [indices, values];
  }
}

class KospiIndexCard extends ContentCard {
  constructor() {
    super();
    this.state = { data: [] };
  }

  async componentDidMount() {
    console.log('componentDidMount');

    var proxyUrl = 'https://cors-anywhere.herokuapp.com/',
      targetUrl = 'https://query1.finance.yahoo.com/v8/finance/chart/%5EKS11?period1=1546268400&period2=1589587200&interval=1d';

    var x = new XMLHttpRequest();
    x.open('GET', proxyUrl + targetUrl);
    
    var self = this;
    x.onload = x.onerror = function() {
      var result = JSON.parse(x.responseText);
      result = result.chart.result[0];
      self.setState({ data: result })
    };
    x.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
    x.send('');
  }

  formatDate(timestamp) {
    var d = new Date(timestamp * 1000),
    month = '' + (d.getMonth() + 1),
    day = '' + d.getDate(),
    year = d.getFullYear();
  
    if (month.length < 2) month = '0' + month;
    if (day.length < 2) day = '0' + day;
  
    return [year, month, day].join('-');
  }

  getValues() {
    var indices = [];
    var values = [];

    var result = this.state.data;
  
    if (result.length == 0) return [indices, values];

    for (var index in result.timestamp) {
      if (index % 3 == 0) {
        indices.push(this.formatDate(result.timestamp[index]));
      } else {
        indices.push('');
      }      
    }

    var adjclose = result.indicators.adjclose[0].adjclose;
    for (var index in adjclose) {
      values.push(parseInt(adjclose[index]));
    }
    return [indices,values];
  }
}

function App() {
  return (
    <div className="App">
      <header className="App-header">
        <Navbar expand='lg'>
          <Navbar.Brand href="#"><h4>잡동사니</h4></Navbar.Brand>
        </Navbar>
        <KospiIndexCard title='코스피 지수' text='코스피 지수를 시각화한다.'></KospiIndexCard>
        <LandPriceCard title='서울 부동산 매매가격지수' text='서울 부동산 매매 가격의 등락을 시각화한다.'></LandPriceCard>
        <ContentCard title='경제 지표의 상관관계' text='경제 지표를 상관관계지수를 통해 시각화한다.'></ContentCard>
        <ContentCard title='주식과 부동산' text='주식과 부동산 어느 것이 투자에 더 유리한가?'></ContentCard>
      </header>
    </div>
  );
}

export default App;
