import React from 'react';

import { Card } from 'react-bootstrap';
import { Line } from "react-chartjs-2";

class ContentCard extends React.Component {
  getValues() {
    var indices = [];
    var values = [];
    for (var i = 0; i < 10; i++) {
      indices.push(i.toString());
      values.push(Math.random() * 50);
    }

    return [indices, values];
  }

  getData() {
    var values = this.getValues();

    const data = {
      labels: values[0],
      datasets: [
        {
          label: '',
          data: values[1],
          fill: false,
          backgroundColor: '#2279B5',
          borderColor: '#2279B5',
          borderWidth: 2,
          pointRadius: 0,
          hitRadius: 20,
          cubicInterpolationMode: 'monotone'
        }
      ]
    };
    return data    
  }

  render() {
    return (
        <Card style={{ width:'94%', marginTop:'15px', marginLeft:'3%' }}>
          <Card.Body>
            <Card.Title>{this.props.title}</Card.Title>
            <Card.Text>{this.props.text}</Card.Text>
            <Line
              data={this.getData()}
              height={150}
              options={{
                legend: {
                  display: false
                },
                scales: {
                  xAxes: [{
                    gridLines: {
                      drawBorder: true,
                      drawOnChartArea: true,
                      lineWidth: 0.0,
                      zeroLineWidth: 0.5
                    }
                  }],
                  yAxes: [{
                    gridLines: {
                      drawOnChartArea: true,
                      lineWidth: 0.2,
                      zeroLineWidth: 0.5
                    }
                  }]
                }
              }}
          />
          </Card.Body>
        </Card>
    )
  }
}

export default ContentCard;